.card--match-statistics {
  .possession {
    padding: 13px 10px 20px;
    text-align: center;

    .title {
      margin-bottom: $spacer-3;
    }

    .graph {
      .progress {
        margin-bottom: $spacer-2;
      }

      .progress-bar {
        background-color: $ls-primary;
      }
    }

    .legend {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: $ls-font-size-large;

      .percent {
        color: $ls-text-color-grayed;
      }

      .percent-highlighted {
        color: $ls-primary;
        font-weight: bold;
      }
    }
  }

  .table {
    .prog {
      text-align: right;
      width: 30%;

      .progress {
        margin: 0;
        background-color: transparent;
        box-shadow: none;

        .progress-bar {
          box-shadow: none;
          border-radius: 2px;
          min-width: 1px;
        }
      }
    }

    .statistic {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .statistic-bars {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;

      .progress {
        background-color: transparent;
        box-shadow: none;
        width: 35%;
        height: 8px;
        border-radius: 0;
        margin-bottom: 0;
        margin-right: 3px;

        &:last-child {
          margin-right: 0;
        }

        &:first-child {
          justify-content: flex-end;
        }

        .progress-bar {
          background-color: $ls-primary;
          box-shadow: none;
        }

        &.gray .progress-bar {
          background-color: #d8d8d8;
        }
      }
    }

    .number {
      width: 2%;
      text-align: center;
      font-weight: $ls-font-weight-bold;
      color: $ls-text-color-grayed;
    }
  }
}
